import React, { useState } from 'react';


function Contact() {
const [data,setdata] = useState({
      fullname:"",
      phone:"",
      email:"",
      message:"",
  }
);
const formSubmit = (e) =>{
      e.preventDefault();
      alert(`my name is ${data.fullname}. My Mobile number is ${data.phone} and email is ${data.email}. And here is what i wanted to show ${data.message}. `)
}
const InputEvent = (event) =>{
const { name, value}= event.target;

setdata((preval)=>{
  return {
    ...preval,
    [name]: value,
  }
})
}

  
  return (
<>
<div className="my-5">
<h1 className="text-center"> Contact Us</h1>
</div>
<div className="container contact_div">
  <div className="row">
    <div className="col-md-6 col-10 mx-auto">
     
      <form onSubmit={formSubmit}>
  <div class="form-group">
    <label for="exampleFormControlInput1">Full Name</label>
    <input type="text" class="form-control" id="exampleFormControlInput1" name="fullname" value={data.fullname} onChange={InputEvent} placeholder="Enter Name"/>
  </div>
  <div class="form-group">
    <label for="exampleFormControlInput1">Phone</label>
    <input type="number" class="form-control" id="exampleFormControlInput1" name="phone" value={data.phone} onChange={InputEvent} placeholder="Enter mobile number"/>
  </div>
  <div class="form-group"  >
    <label for="exampleFormControlInput1">Email address</label>
    <input type="email" class="form-control" id="exampleFormControlInput1" name="email" value={data.email} onChange={InputEvent} placeholder="name@example.com"/>
  </div>
  <div class="form-group"  >
    <label for="exampleFormControlInput1">Message</label>
    <input type="text" class="form-control" id="exampleFormControlInput1" name="message" value={data.message} onChange={InputEvent} placeholder="Write Message"/>
  </div>
  <div className="col-6 my-3">
  <button class="btn btn-outline-primary" type="submit">Submit</button>
  </div>
</form>
      
    </div>
  </div>
</div>
</>
 );
}

export default Contact;
